import React, { useState } from 'react';
import CSRFToken from '../../components/CSRFToken';
import { Redirect, Link } from 'react-router-dom';
import { Container } from '@material-ui/core';
import { connect } from 'react-redux';
import { register } from '../../actions/auth';
import './Register.css'
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@mui/material/Button';
import AssignmentTurnedInTwoToneIcon from '@mui/icons-material/AssignmentTurnedInTwoTone';
import Typography from '@mui/material/Typography';

const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            width: '25ch'
        },
    },
}));

const Register = ({ register, isAuthenticated }) => {
    const classes = useStyles();

    const[formData, setFormData] = useState({
        username: '',
        password: '',
        re_password: ''
    });

    const [values, setValues] = React.useState({
        amount: '',
        password: '',
        weight: '',
        weightRange: '',
        showPassword: false,
    });

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClickShowPassword = () => {
        setValues({ ...values, showPassword: !values.showPassword });
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    const[accountCreated, setAccountCreated] = useState(false);

    const { username, password, re_password } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        if(password === re_password){
            register(username, password, re_password);
            setAccountCreated(true);
        }
    }

    if (isAuthenticated) {
        return <Redirect to='/dashboard' />;
    }else if (accountCreated) {
        return <Redirect to='/login' />;
    }

    const myError = () => {
        var e = false;
        
        if (values.password.length === 0) {
            e = false
        }else if (values.password.length > 0 && values.password.length < 6) {
            e = true
        }else{
            e = false
        }

        return e
    }

    const myErrorLabel = () => {
        var label = 'Contrase√±a';
        
        if (values.password.length === 0) {
            label = 'Contrase√±a';
        }else if (values.password.length > 0 && values.password.length < 6) {
            label = 'Error';
        }else{
            label = 'Contrase√±a';
        }

        return label;
    }

    const myErrorLabelWith = () => {
        var labelWith = 95;
        
        if (values.password.length === 0) {
            labelWith = 95;
        }else if (values.password.length > 0 && values.password.length < 6) {
            labelWith = 45;
        }else{
            labelWith = 95;
        }

        return labelWith;
    }

    const myErrorMessage = () => {
        var errorMessage = '';
        
        if (values.password.length === 0) {
            errorMessage = '';
        }else if (values.password.length > 0 && values.password.length < 6) {
            errorMessage = 'La contrase√±a debe ser mayor o igual a 6 caracteres';
        }else{
            errorMessage = '';
        }

        return errorMessage;
    }
    
    return (
        <>
            <Container>
                <Typography className="titleRegister" variant="h3" gutterBottom component="h3">
                    Registro de Nuevo Usuario ‚úç
                </Typography>
                <form onSubmit={e => onSubmit(e)} className={classes.root, 'input-user'} noValidate autoComplete="on">
                    <CSRFToken />
                    <TextField 
                        id="outlined-basic" 
                        label="Usuario" 
                        variant="outlined" 
                        name="username"
                        onChange={e => onChange(e)}
                        value={username}
                        required 
                    />
                    <br/>
                    <FormControl className="input-password"
                                error={myError()}
                                variant="outlined"
                                required
                    >
                        <InputLabel htmlFor="outlined-adornment-password">{myErrorLabel()}</InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-password"
                            name='password'
                            type={values.showPassword ? 'text' : 'password'}
                            value={values.password, password}
                            minLength='6'
                            onChange={handleChange('password'), e => onChange(e)}
                            endAdornment={
                            <InputAdornment position="end">
                                <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={handleClickShowPassword}
                                    onMouseDown={handleMouseDownPassword}
                                    edge="end"
                                >
                                {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                </IconButton>
                            </InputAdornment>
                            }
                            labelWidth={myErrorLabelWith()}
                        />
                        <FormHelperText id="outlined-weight-helper-text">{myErrorMessage()}</FormHelperText>
                    </FormControl>
                    <br />
                    <FormControl className="input-password"
                                error={myError()}
                                variant="outlined"
                                required
                    >
                        <InputLabel htmlFor="outlined-adornment-repassword">Confirmar Contrase√±a</InputLabel>
                            <OutlinedInput
                                id="outlined-adornment-repassword"
                                name='re_password'
                                type={values.showPassword ? 'text' : 'password'}
                                value={values.password, re_password}
                                minLength='6'
                                onChange={handleChange('re_password'), e => onChange(e)}
                                endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                    >
                                    {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                                }
                                labelWidth={170}
                            />
                            <FormHelperText id="outlined-weight-helper-text">{myErrorMessage()}</FormHelperText>
                    </FormControl>
                    <br />
                    <Button type="submit"
                            className="createButton" 
                            variant="contained" 
                            endIcon={<AssignmentTurnedInTwoToneIcon />}>
                        Registrarme
                    </Button>
                </form>
                <Typography className="noteForm" variant="overline" display="block" gutterBottom>
                    ¬øYa estas registrado? <Link className="linkText" to='/login'>Inicia Sesi√≥n</Link>
                </Typography>
            </Container>
        </>
    )
}

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { register })(Register);
